<script>
	import { fade } from 'svelte/transition';

	export let container = undefined;
	export let annotations = [];
	export let datum;

	const vals = ['top', 'right', 'bottom', 'left', 'width','max-width', 'font-size', 'font-style', 'text-align', 'margin', 'background-color', 'color'];

	$: fillStyle = d => {
		let style = '';
		vals.forEach(val => {
			if (d[val]) {
				style += `${val}:${d[val]};`;
			}
		});
		return style;
	};

</script>


{#each annotations as d, i}
	{#if datum && datum.index === d.index }
		<div class="layercake-annotations" bind:this={container} style="{fillStyle(d)}">
			<p  transition:fade
				class="layercake-annotation indicator"
				data-id="{i}"
				>{d.indicator}</p>
			<p  transition:fade
				class="layercake-annotation unit"
				data-id="{i}"
				>{d.unit}</p>
			<p  transition:fade
				class="layercake-annotation source"
				data-id="{i}"
				>{d.source}</p>
		</div>
	{/if}
{/each}


<style>
	.layercake-annotations {
		position: absolute;
	}
	@media (max-width: 350px) {
		.layercake-annotation.indicator {
			font-size:1.6em;
			margin-bottom:  0.3em;
		}
	}
	@media (min-width: 350px) and (max-width: 500px) {
		.layercake-annotation.indicator {
			font-size:1.8em;
			margin-bottom: 1.6em;
		}
	}
	@media (min-width: 500px) {
		.layercake-annotation.indicator {
			font-size:1.8em;
			margin-bottom: 1.8em;
		}
	}
	.layercake-annotation.indicator {
		text-align: center;
	}
	.layercake-annotation.unit {
		margin:0;
	}
	.layercake-annotation.source {
		font-size:0.8em;
		font-style:italic;
		margin:0;
		color:#5B717F;
	}
</style>
