<script>
  import { getContext, onMount } from 'svelte';
  import { LayerCake, Svg, Html } from 'layercake';
  import { tweened } from 'svelte/motion';
  import * as eases from 'svelte/easing';

  import FadedAnnotations from '../components/FadedAnnotations.svelte';
  import ForceBubbles from '../components/ForceBubbles.svelte';

  export let data = [];
  export let mobile = false;
  export let step;
  export let display;
  export let innerWidth;
  export let innerHeight;

  const colours = ['#212529','#FFF']; //#E9ECEF
  const radius = 5;

  const annotations = data.map(d => (
    { indicator: d.indicator,
      unit: d.unit,
      source: d.source,
      index: d.index,
      top:mobile ? '5vh' : '15vh',
      left:'10vw',
      width:'80vw',
      'text-align':mobile ? '' : 'center'
    }
  ))
  
  const tweenOpacity = {
    duration: 500,
    easing: eases.cubicInOut
  };
  const tweenBubbles = {
    duration: 1000,
    easing: eases.cubicInOut
  };
  const tweenPercentage = {
    duration: 3000,
    easing: eases.cubicInOut
  };
  
  let opacity = 0;
  let bubbles = Array.from({length: 200},(d,i) => ({
    x:i<100 ? innerWidth + Math.random()*innerWidth/2 : -innerWidth-Math.random()*innerWidth/2, 
    y:mobile ? innerHeight : Math.random()*innerHeight/2,
    r:radius, 
    fill:i<100 ? colours[0] : colours[1],
    fillOpacity: 1,
    stroke: i<100 ? colours[1] : colours[0],
    strokeOpacity: 1,
    id: i%100,
    category: i<100 ? 'black' : 'white'
  }));

  const tweenedOpacity = tweened(opacity, tweenOpacity);
  const tweenedBubbles = tweened(bubbles, tweenBubbles);
  const black_percentage = tweened(0, tweenPercentage);
  const white_percentage= tweened(0, tweenPercentage);

  function updateBubbles(event) {
    bubbles = event.detail;
  }
  function updatePercentages(datum) {
    black_percentage.set(datum ? +datum.black : 100);
    white_percentage.set(datum ? +datum.white : 100);
  }

  $: tweenedOpacity.set(opacity);
  $: tweenedBubbles.set(bubbles);

  $: opacity = display ? 1 : 0;
  $: datum = data.find(d => d.index == step);
  $: updatePercentages(datum);
  
</script>

<div class='chart-container'>
	<LayerCake data={datum} >
    	<Svg>
        <g class='bubbles' style='opacity: {$tweenedOpacity};'>
            {#each $tweenedBubbles as {x, y, r, fill, fillOpacity, stroke, strokeOpacity, id, category},i}
              {#if !datum || category == 'black' &&  Math.round($black_percentage) > id || category == 'white' &&  Math.round($white_percentage) > id}
                <circle
                  cx={x}
                  cy={y}
                  {r}
                  {fill}
                  fill-opacity={fillOpacity}
                  {stroke}
                  stroke-opacity={strokeOpacity}
                />
              {/if}
            {/each}
        </g>
        <ForceBubbles {bubbles} {step} on:message={updateBubbles} dimensions={[innerWidth,innerHeight]} {mobile}/>
      </Svg>
      <Html>
        <FadedAnnotations {annotations} {datum} />
      </Html>
	</LayerCake>
</div>