<script>
	import { getContext, createEventDispatcher } from 'svelte';
	import { forceSimulation, forceManyBody, forceCollide, forceX, forceY } from 'd3-force';

  	const { data, width, height } = getContext('LayerCake');
  	const dispatch = createEventDispatcher();

  	export let bubbles = [];
  	export let mobile = false;
  	export let step;
  	export let dimensions;

  	let x;
  	let y;
  	let simulation = forceSimulation(bubbles).alphaDecay(0)

  	function waffle(start, size, nbPointsPerDimension, i){
  		x = start[0] + (size[0]/nbPointsPerDimension[0])*(i%nbPointsPerDimension[0]);
  		y = start[1] + (size[1]/nbPointsPerDimension[1])*(Math.floor(i/nbPointsPerDimension[1]));
  		return [x,y];
  	}
 
  	function runForce(step){
  		switch (step) {
  			case undefined:
  				/* Initialisation */
  				setTimeout(() => {
  					bubbles.forEach((d,i) => {
	  					d.x = i<100 ? dimensions[0] + Math.random()*dimensions[0]/2 : -dimensions[0] - Math.random()*dimensions[0]/2;
					    d.y = mobile ? dimensions[1] : Math.random()*dimensions[1]/2;
	  					})
  					dispatch('message', bubbles)}, 1000); 
  				
  			break;

	  		case 0:
	  			/* Introduction */
	  			simulation
			  		.force("charge", forceManyBody().strength(-10))
					.force("forceX", forceX(dimensions[0]/2).strength(0.1))
					.force("forceY", forceY(dimensions[1]/2).strength(0.1))
					.force("collide", forceCollide(d => d.r))
					.on('tick', () => {
						simulation.tick();
						dispatch('message', [...bubbles]);
					})
	  		break;

	  		default:
	  			/* Waffle */
	  			simulation
	  				.force("charge", forceManyBody().strength(0))
					.force("forceX", forceX((d,i) => i<100 ? waffle(blackWaffleStart, waffleDimensions, nbPointsPerDimension, i)[0] : 
															 waffle(whiteWaffleStart, waffleDimensions, nbPointsPerDimension, i%100)[0]).strength(0.05))

					.force("forceY", forceY((d,i) => i<100 ? waffle(blackWaffleStart, waffleDimensions, nbPointsPerDimension, i)[1] : 
															 waffle(whiteWaffleStart, waffleDimensions, nbPointsPerDimension, i%100)[1]).strength(0.05))
					.on('tick', () => {
						simulation.tick();
						dispatch('message', [...bubbles]);
					})			
	  	}
  	}

  	$: nbPointsPerDimension = mobile ? [10,10] : [10,10];
  	$: blackWaffleStart = mobile ? [dimensions[0]*0.1,dimensions[1]*0.45] : [dimensions[0]*0.20,dimensions[1]*0.45];
  	$: whiteWaffleStart = mobile ? [dimensions[0]*0.55,dimensions[1]*0.45] : [dimensions[0]*0.60,dimensions[1]*0.45];
  	$: waffleDimensions = mobile ? [dimensions[0]*0.35,dimensions[0]*0.35] : [dimensions[0]*0.20,dimensions[0]*0.20];

	$: runForce(step)
  		
</script>