<script>
  import { onMount } from 'svelte';

  import DraggableLabel from './components/DraggableLabel.svelte';

  import ethnicityCsv from './data/ethnicity-stats.csv';
  import ScrollyStory from './graphics/scrolly_story.svelte';

  export let pymChild = {};
  export let styleNs = false; // serif or san-serif;
  export let title = "NSMG-016";

	const mobileWidthThreshold = 500;
  let innerWidth;
  let innerHeight;
  let step;
  let display;

  onMount(() => {
		pymChild.sendHeight();
    pymChild.sendMessage('childLoaded', 'ready');
    pymChild.onMessage('step', s => step = +s);
    pymChild.onMessage('display', d => display = JSON.parse(d));
  });

  $: step = display ? step : undefined;
  $: console.log('step', step);

</script>

<svelte:head>
  <title>{title}</title>
</svelte:head>

<svelte:window 
  bind:innerWidth
  bind:innerHeight
/>

<div class="embed-container" class:styleNs>

    <div class="background">
      {#if innerWidth > mobileWidthThreshold}
        <ScrollyStory mobile={false} data={ethnicityCsv} {step} {display} {innerWidth} {innerHeight} />
      {:else if innerWidth <= mobileWidthThreshold}
        <ScrollyStory mobile={true} data={ethnicityCsv} {step} {display} {innerWidth} {innerHeight}/>
      {/if}

    </div>

</div>


<style>
  .embed-container.styleNs {
    font-family: "Merriweather", sans-serif; /* catch IE not loading fonts */
  }

